import React, { PureComponent, Component, Fragment, cloneElement, createElement } from 'react';
import { BrowserRouter, StaticRouter, Route, Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import routes from '../routes';
import Nav from './Nav';
import UsersProvider from '../../api/users/provider';
import ProjectsProvider from '../../api/projects/provider';
import { Provider as ThemesProvider } from '../contexts/themes';
import MyRouter from '../Router';

const Providers = [UsersProvider, ProjectsProvider];

class DataProviders extends Component {

    render() {
        console.log('this.props', this.props);

        return Providers.reduce((acc, C) => {
            if (acc.props.children) {
                const { children } = acc.props;
                return cloneElement(acc, {}, cloneElement(children, {}, createElement(C, {}, this.props.children)));
            }
            return cloneElement(acc, {}, createElement(C, {}, this.props.children));
        }, <Fragment />);
    }
}

class ConfigProviders extends Component {
    render() {
        console.log('config props', this.props);

        return (
            <Fragment>
                <ThemesProvider>
                    {this.props.children}
                </ThemesProvider>
            </Fragment>
        );
    }
}

class Layout extends Component {
    render() {
        console.log('Layout props', this.props);

        return (
            <Fragment>
                <Nav />
                {routes.map(route => <Route key={route.key} {...route} />)}
                <div>default footer</div>
            </Fragment>
        );
    }
}

const App = (props) => {
    const Router = global.window ? BrowserRouter : StaticRouter;
    console.log('app props',props);

    return (
        <DataProviders>
            <ConfigProviders>
                <Router>
                    <Layout />
                </Router>
            </ConfigProviders>
        </DataProviders>
    );
};

const Home = () => (
    <div>
        <h2>Home</h2>
    </div>
);

const About = () => (
    <div>
        <h2>About</h2>
        <Users.Consumer>
            {props => <Home/>}
        </Users.Consumer>
    </div>
);

const Topics = ({ match }) => (
    <div>
        <h2>Topics</h2>
        <ul>
            <li>
                <Link to={`${match.url}/rendering`}>Rendering with React</Link>
            </li>
            <li>
                <Link to={`${match.url}/components`}>Components</Link>
            </li>
            <li>
                <Link to={`${match.url}/props-v-state`}>Props v. State</Link>
            </li>
        </ul>

        <Route path={`${match.url}/:topicId`} component={Topic}/>
        <Route
            exact
            path={match.url}
            render={() => <h3>Please select a topic.</h3>}
        />
    </div>
);

const Topic = ({ match }) => (
    <div>
        <h3>{match.params.topicId}</h3>
    </div>
);

export default App;
